.prt-footer__wrap {
  @include flex;
  justify-content: space-between;
  align-items: center;
}

.prt-footer__contacts {
  @include nl;
  @include flex;
  justify-content: center;
  align-items: center;
}

.prt-footer__contacts-wrap {
  width: 220px; 
}

.prt-footer__phone {
  order: 1;
}

.prt-footer__info {
  order: 2;
}

.prt-footer__bot-txt {
  text-align: center;
}

@media #{$tablet} {
  .prt-footer__wrap {
    @include flex-row;
  }
  
  .prt-footer__bot-txt {
    text-align: left;
  }
  
  .prt-footer__phone {
    order: 3;
  }
  
  .prt-footer__info {
    order: 0;
  }
  
  .prt-footer__contacts {
    flex-direction: column;
  }
  
  .prt-footer__contacts-wrap {
    width: auto;
  }
}