.video__wrap {
  padding: 20px;
}

.video__ttl {
  @include line-p;
}

.video__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  margin-top: 20px;
  justify-content: center;
}

.video__item {
  height: 300px;
  width: 280px;
  background: $color-1;
  margin-bottom: 20px;  
  margin-right: 10px;
}


@media #{$tablet} {
  .video__wrap {
    max-width: $max-tablet;
    margin: auto;
    margin-top: 20px;
  }
  
  .video__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .video__items {
    margin-top: 35px;    
  }

  .video__item {
    width: 352px;
    margin-right: 20px;
    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }
}


@media #{$desktop} {
  .video__wrap {
    max-width: $max-desktop;
    margin-top: 30px;
    padding-bottom: 0;
  }
  
  .video__ttl {
    font-size: $h2-d;
    letter-spacing: -0.8px;
    &:before,
    &:after {
      width: 70px;
      margin-right: 20px;
      margin-left: 20px;
    }
  }
  
  .video__items {
    margin-top: 60px;
    justify-content: flex-start;
    padding-left: 20px;
  }
  
  .video__item {
    width: 542px;
    margin-right: 50px;
    &:nth-of-type(2n) {
      margin-right: 0px;
    }
  }
}