.order-prt-service__wrap {
  padding: 20px;
  margin-bottom: 30px;
}

.order-prt-service__ttl {
  font-size: $h2-m;
  text-align: center;
}

.order-prt-service__txt-top {
  display: inline-block;
  margin-right: 5px;
}

.order-prt-service__txt {
  display: inline-block;
}

.order-prt-service__txt-bg {
  padding: 15px;
  background: url(%=static=%/content/prt-srv-txt-bg.png)no-repeat 50%;
  color: #fff;
  background-size: contain;
}

.order-prt-service__link {
  font-weight: normal;
  @include btn;
  width: 280px;
  margin: auto;
}

@media #{$tablet} {
  .order-prt-service__ttl{
    text-transform: uppercase;
  }
}


@media #{$desktop} {
  .order-prt-service__wrap {
    max-width: $max-desktop;
    margin: auto;
    @include flex-row;
    padding-left: 17%;    
  }
  
  .order-prt-service__ttl {
    font-size: $h2-d;
    text-transform: uppercase;
    text-align: left;
  }    
  
  .order-prt-service__txt {
    display: block;
  }
  
  .order-prt-service__link {
    width: 350px;
    padding-top: 13px;
    padding-bottom: 13px;
    margin-left: 70px;
  }
}