.txt-ttl__wrap {
  padding: 20px;
}

.txt-ttl__ttl {
  @include line-p;
}

.txt-ttl__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.txt-ttl__item-block {
  width: 300px;
  height: 300px;
  background: $color-5;
}

@media #{$tablet} {
  .txt-ttl__wrap {
    margin: auto;
    margin-top: 40px;
    max-width: $max-tablet;    
  }
  .txt-ttl__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  .txt-ttl__items {
    align-items: flex-start;
  }
  
  .txt-ttl__item-block {
    width: 583px;
  }
}


@media #{$desktop} {
  .txt-ttl__wrap {
    max-width: $max-desktop;
  }
  
  .txt-ttl__items {
    flex-wrap: nowrap;
    align-items: flex-start;
    justify-content: space-between;
  }  
  
  .txt-ttl__item-block {
    height: auto;
    min-height: 366px;
  }
  
  .txt-ttl__item-info {
    width: 565px;
  }  
  
  .txt-ttl__h4 {
    margin: 0;
  }
  
  .txt-ttl__txt-item {
    margin-bottom: 8px;
  }
}

