.ourService__wrap {
  padding: 20px;
}

.ourService__ttl {
  @include line-p;
  font-size: $h2-m; 
}

.ourService__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  margin-top: 40px;
}

.ourService__item {
  @include flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease;
  width: 280px;
  padding-bottom: 30px;
  margin-top: 30px;
  &:hover {
    transition: 0.3s ease;
    box-shadow: 0px 10px 15px 0px rgba(0,0,0, 0.1);
  }
  &:hover .ourService__img-block {    
    border-top: 1px solid #f2f2f2;
    border-bottom-color: #fff;
  }
}

.ourService__img-block {
  @include flex;
  padding-top: 20px;
  padding-bottom: 20px;
  justify-content: center;
  align-items: center;
  border: 1px solid #f2f2f2;
  width: 280px;
  height: 282px;
}

.ourService__txt {
  font-family: $font-1;
  font-size: 20px;
  text-align: center;
  padding: 0;
  margin: 0;
  margin-top: 30px;
}

.ourService__price {
  font-family: $font-2;
  font-size: 24px;
  margin-top: 10px;
}

.ourService__link {
  @include btn;
  width: 240px;
  font-size: 16px;
  font-family: $font-1;
  border-width: 2px;
  margin-top: 10px;
  &:hover {
    color: $color-1;
  }
}

.ourService__link-size {
  display: block;
  margin: auto;
  font-family: $font-1;
  color: $color-2;
  font-size: 18px;
  text-align: center;
  margin-top: 20px;
}


@media #{$tablet} {
  .ourService__wrap {
    max-width: $max-tablet;
    margin: auto;
    margin-top: 30px;
  }
  
  .ourService__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
  }
}


@media #{$desktop} {
  .ourService__wrap {
    max-width: $max-desktop;
    margin-top: 50px;
  }
  
  .ourService__ttl {
    font-size: $h2-d;
    margin-right: 20px;
    margin-left: 20px;
    &:before,
    &:after {
      width: 70px;
    }
  }
  
  .ourService__items {
    justify-content: center;
    margin-top: 58px;    
  }
  
  .ourService__item {
    width: 340px;    
    margin: 0;   
    margin-right: 70px;
    &:nth-of-type(3n) {
      margin-right: 0px;
    }
  }
  
  .ourService__img-block {
    width: 340px;
    padding-top: 42px;
  }
  
  .ourService__txt {
    margin-top: 45px;
  }
  
  .ourService__price {
    margin-top: 7px;
  }
  
  .ourService__link {
    margin-top: 27px;
    padding-top: 8px;
    padding-bottom: 8px;
    width: 275px;
  }
  
  
  .ourService__link-size {
    margin-top: 30px;
    padding-right: 60px;
  }
}

