.prt-service__wrap {
  padding: 20px;
  margin-top: 10px;
}

.prt-service__ttl {
  font-family: $font-2;
  font-size: $h2-m;
  @include line-p;
  color: $color-1;
}

.prt-service__items {
  @include nl;
  margin-top: 40px;
  @include flex-row;
  flex-wrap: wrap;
  justify-content: center;
}

.prt-service__item {
  @include flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  padding-bottom: 30px;
  margin-top: 30px;
  margin-right: 15px;
  margin-left: 15px;
  &:hover {
    transition: 0.3s ease;
    box-shadow: 0px 10px 15px 0px rgba(0,0,0, 0.1);
  }
  &:hover .prt-service__item-img {    
    border-top: 1px solid #f2f2f2;
    border-bottom-color: #fff;
  }
}

.prt-service__item-img {
  @include flex;
  padding-top: 20px;
  padding-bottom: 20px;
  justify-content: center;
  align-items: center;
  border: 1px solid #f2f2f2;
  width: 280px;
  height: 282px;
}

.prt-service__item-txt {
  font-size: 16px;
  color: $color-1;
  margin-top: 20px;
}

.prt-service__link {
  @include btn;
  margin-top: 30px;
  width: 260px;
  border-width: 2px;
  font-size: 14px;
  font-family: $font-1;
}


@media #{$desktop} {  
  .prt-service__wrap {
    max-width: $max-desktop;
    margin: auto;
    padding-bottom: 50px;
    margin-top: 20px;
  }
  
  .prt-service__ttl {
    font-size: $h2-d;
    text-transform: uppercase;
    &:after,
    &:before {
      width: 70px;
      margin-right: 20px;
      margin-left: 20px;
    }
  }
  
  .prt-service__item {
    margin-bottom: -40px;
    background: #fff;
    width: 252px;
    margin-right: 50px;
    margin-left: 0px;
    &:nth-of-type(4n) {
      margin-right: 0px;
    }
    &:hover {
      position: relative;
      z-index: 100;
    }
    &:hover .prt-service__link {
      opacity: 1;
    }    
  }  
  
  .prt-service__item-img {
    width: 252px;
  }
  
  .prt-service__link {
    opacity: 0;
    width: 200px;
  }
}

