.prt-service-info__wrap {
  padding: 20px;
  padding-top: 0;
}

.prt-service-info__txt {
  font-family: $font-1-l;
  font-size: 17px;
  margin: 0;
  line-height: 26px;
}

.prt-service-info__ttl {
  font-size: $h4-m;
  margin-top: 10px;
  margin-bottom: 10px;
}

.prt-service-info__items {
  @include nl;
  padding-left: 10px;
  margin-top: 10px;
}

.prt-service-info__item {
  @include flex-row;
  font-size: 17px;
  font-family: $font-1-l;
  color: $color-1;
  margin-bottom: 5px;
}

.prt-service-info__item-line {
  height: 2px;
  width: 6px;
  background: $color-2;
  margin-top: 9px;
  margin-right: 10px;
}

@media #{$desktop} {
  .prt-service-info__wrap {
    max-width: $max-desktop;
    margin: auto;
  }
}


