.rewiews__wrap {
  padding: 20px;
}

.rewiews__ttl {
  @include line-p;  
}

.rewiews__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;  
  justify-content: center;
}

.rewiews__item {
  @include flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-right: 10px;
}

.rewiews__img-block {
  height: 170px;
  width: 170px;
  border-radius: 50%;  
  overflow: hidden;
}

.rewiews__info {
  @include flex;  
}

.rewiews__name {
  font-family: $font-2;
  text-align: center;
  font-size: 18px;
  color: $color-1;
  margin-top: 20px;
}

.rewiews__txt {
  font-family: $font-1-l;
  color: $color-1;  
  width: 280px;
}

.rewiews__link {
  font-size: 16px;
  color: $color-2;  
}

.rewiews__show-all {
  display: block;
  margin: auto;
  color: $color-2;
  text-align: center;
  margin-top: 30px;
}


@media #{$tablet} {
  .rewiews__wrap {
    max-width: $max-tablet;
    margin: auto;
    margin-top: 30px;    
  }
  
  .rewiews__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .rewiews__item {
    width: 50%;
    margin: 0;
    margin-bottom: 30px;
  }
  
  .rewiews__items {
    margin-top: 40px;
  }
}


@media #{$desktop} {
  .rewiews__wrap {
    max-width: $max-desktop;
    margin-top: 55px;    
  }
  
  .rewiews__ttl {
    font-size: $h2-d;
    letter-spacing: -0.8px;    
    &:before,
    &:after {
      width: 70px;  
      margin-right: 20px;
      margin-left: 20px;
    }    
  }
  
  .rewiews__items {
    margin-top: 20px;
    padding-left: 40px;
    justify-content: flex-start;
  }
  
  .rewiews__item {
    flex-direction: row;    
    justify-content: flex-start;
    width: 485px;
    margin-right: 135px;
    margin-bottom: 47px;
    &:nth-of-type(2n) {
      margin-right: 0px;
    }
  }
  
  .rewiews__img-block {
    margin-right: 23px;
  }
  
  .rewiews__name {
    margin-top: 27px;
    text-align: left;
  }
  
  .rewiews__txt {
    width: 290px;
    line-height: 21px;
  }
  
  .rewiews__show-all {
    margin-top: 8px;
    font-size: 18px;
  }
}

