.action {
  background: #383838;
}

.action__wrap {  
  padding: 20px;
}

.action__ttl {
  color: #fff;
  font-family: $font-2;
  font-size: 20px;
  text-transform: uppercase;
}

.action__ttl-y {
  color: $color-2;
}

.action__items {
  @include nl;
  margin-top: 20px;
}

.action__item {
  @include flex-row;
  font-family: $font-1-l-italic;
  font-size: 18px;
}

.action__item-nbr {
  font-family: $font-1-m;
  margin-right: 7px;
  color: $color-2;
}

.action__item-txt {
  color: #fff;
}

.action__item-price {
  text-decoration: line-through;
}

.action__time {
  margin-top: 20px;
}

.action__time-ttl {
  font-family: $font-1-m;
  font-size: 20px;
  color: $color-2;
}

.action__time-timer {
  @include flex-row;
  margin-top: 20px;
}

.action__hour {  
  &:after {
    content: '';
    height: 23px;
    width: 2px;
    display: inline-block;
    background: #5f5f5f;
    margin-left: 9px;
    margin-right: 9px;
  }
}

.action__minute {
  &:after {
    content: '';
    height: 23px;
    width: 2px;
    display: inline-block;
    background: #5f5f5f;
    margin-left: 9px;
    margin-right: 9px;
  }
}

.action__time-nbr { 
  font-family: $font-1-m;
  font-size: 23px;
  padding-right: 5px;
  color: #fff;  
  vertical-align: bottom;    
}

.action__time-txt {
  font-family: $font-1;
  font-size: 20px;
  color: #d7d7d7;
}

.action__arrow {
  display: none;
  @include bg($action-arrow);
  margin-left: auto;
}

.action__link {
  margin-top: 30px;
  @include btn;
  margin-bottom: 20px;
}


@media #{$tablet} {
  .action__wrap {
    @include flex-row;
    max-width: $max-tablet;
    margin: auto;
  }
  
  .action__info {
    width: 50%;
  }
  
  .action__ttl {
    font-size: 18px;
  }
  
  .action__time {
    @include flex;
    width: 50%;
    align-items: flex-end;
  }
  
  .action__item {
    font-size: 16px;
  }
  
  .action__arrow {
    display: block;
    margin-top: 20px;
  }
  
  .action__link {
    width: 280px;
    margin-top: 0;
    margin-right: 60px;
  }
}


@media #{$desktop} {
  .action__wrap {
    max-width: $max-desktop;
    margin-top: 30px;
    padding-top: 20px;
    padding-right: 10px;
    padding-bottom: 40px;
  }
  
  .action__ttl {
    font-size: 36px;
    line-height: 39px;
    letter-spacing: -0.7px;
  }
  
  .action__info {
    width: 690px;    
    padding-left: 10px;    
  }
  
  .action__time {
    width: 500px;
  }
  
  .action__ttl-y--b {
    font-size: 40px;
  }
  
  .action__items {
    padding-left: 18px;
  }
  
  .action__item {
    font-size: 22px;
    margin-bottom: 10px;
  }
  
  .action__time-timer {
    margin-top: 30px;
  }
  
  .action__time-ttl {
    font-size: 27px;
    margin-top: 25px;
  }
  
  .action__time-nbr {
    font-size: 36px;
  }
  
  .action__time-txt {
    font-size: 22px;
  }  
  
  .action__hour {
    margin-right: 15px;
    &:after {
      height: 30px;
      margin-left: 20px;
      margin-right: 0px;
    }
  }
  
  .action__minute {
    margin-right: 15px;
    &:after {
      height: 30px;
      margin-left: 20px;
      margin-right: 0px;
    }
  }
  
  .action__arrow {
    margin-right: 20px;
  }
  
  .action__link {
    margin-right: 90px;
    width: 370px;
  }
}





